import { useState, useEffect } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import Navbar from '../components/Navbar';
// import { Search, AlertTriangle, Info, Clock, MapPin, TrainFront, RefreshCw } from 'lucide-react'; // Example icons

export default function TrainStatus() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const initialTrain = searchParams.get('train') || '';

  const [trainInput, setTrainInput] = useState(initialTrain);
  const [trainStatus, setTrainStatus] = useState(null);
  const [trainList, setTrainList] = useState([
    { train_number: '12001', train_name: 'SHATABDI EXP', source: 'NDLS', destination: 'BPL' },
    { train_number: '12951', train_name: 'MUMBAI RAJDHANI', source: 'MMCT', destination: 'NDLS' },
    { train_number: '22439', train_name: 'VANDE BHARAT EXP', source: 'NDLS', destination: 'SVDK' },
  ]);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (initialTrain) {
      // Trigger search without modifying trainInput state directly here,
      // as handleSearch uses trainInput state.
      // Let handleSearch pick up initialTrain via the state.
      handleSearch(initialTrain);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Run once on mount if initialTrain exists

  const handleSearch = async (trainNumberToSearch) => {
    // Use trainNumberToSearch if provided (e.g., from popular list or initial load),
    // otherwise use the current trainInput state.
    const currentTrainQuery = typeof trainNumberToSearch === 'string' && trainNumberToSearch.trim()
      ? trainNumberToSearch.trim()
      : trainInput.trim();

    if (!currentTrainQuery) {
      setError("Please enter a train number.");
      return;
    }

    setLoading(true);
    setError(null);
    setTrainStatus(null);

    if (trainInput.trim() !== currentTrainQuery) {
        setTrainInput(currentTrainQuery); // Sync state if different
    }

    router.push(`/train-status?train=${currentTrainQuery}`, { scroll: false });

    try {
      const response = await fetch(
        `https://irctc1.p.rapidapi.com/api/v1/liveTrainStatus?trainNo=${currentTrainQuery}&startDay=1`,
        {
          method: 'GET',
          headers: {
            'x-rapidapi-key': process.env.NEXT_PUBLIC_RAPIDAPI_KEY,
            'x-rapidapi-host': 'irctc1.p.rapidapi.com',
          },
        }
      );

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({ message: `HTTP error! Status: ${response.status}` }));
        throw new Error(errorData.message || `HTTP error! Status: ${response.status}`);
      }

      const data = await response.json();

      if (!data.status || !data.data || Object.keys(data.data).length === 0) {
        setError(data.message || 'Train not found or no data available.');
        setTrainStatus(null);
      } else {
        const d = data.data;
        setTrainStatus({
          trainNumber: d.train_number,
          trainName: d.train_name,
          currentStationName: d.current_station_name,
          currentStationCode: d.current_station_code,
          eta: d.eta,
          etd: d.etd,
          platform: d.platform_number,
          delayMinutes: d.delay_minutes,
          statusAsOf: d.status_as_of,
          isRunning: d.is_running,
          upcomingStations: d.upcoming_stations || [],
          previousStations: d.previous_stations || [],
          sourceStationName: d.source_station_name,
          destinationStationName: d.destination_station_name,
        });
        setError(null);
      }
    } catch (err) {
      console.error("Fetch error:", err);
      setError(err.message || 'Failed to fetch train status. Please check your connection or the train number.');
      setTrainStatus(null);
    } finally {
      setLoading(false);
    }
  };

  const handleTrainSelect = (trainNo) => {
    setTrainInput(trainNo); // Update input field
    handleSearch(trainNo);  // Trigger search with the selected number
  }

  return (
    <div className="container min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-slate-50 py-8 px-4 font-[Inter,system-ui,sans-serif]">
      <Navbar />
      <div className="card max-w-4xl mx-auto mt-8 bg-white rounded-2xl shadow-xl border border-slate-200 overflow-hidden">
        <header className="header bg-gradient-to-r from-blue-600 to-indigo-700 p-8 text-white">
          <h1 className="title text-3xl md:text-4xl font-bold tracking-tight">
            Live Train Status
          </h1>
          <p className="subtitle mt-2 text-blue-200 text-base md:text-lg leading-relaxed">
            Track your train in real-time with accurate updates.
          </p>
        </header>

        <main className="content p-6 md:p-8">
          <div className="searchBox relative mb-8">
            <input
              type="text"
              placeholder="Enter Train Number (e.g., 12345)"
              className="input w-full pl-5 pr-[130px] md:pr-[150px] py-3.5 text-slate-800 bg-slate-50 border-2 border-slate-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all duration-150 ease-in-out text-base placeholder-slate-400"
              value={trainInput}
              onChange={(e) => setTrainInput(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
            />
            <button
              onClick={() => handleSearch()}
              disabled={loading || !trainInput.trim()}
              className="searchButton absolute right-2 top-1/2 transform -translate-y-1/2 px-4 md:px-5 py-2.5 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500 transition-colors duration-150 ease-in-out disabled:bg-slate-400 disabled:cursor-not-allowed flex items-center"
            >
              {loading ? (
                <svg className="animate-spin -ml-1 mr-2 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
              ) : null}
              {loading ? 'Searching...' : 'Search'}
            </button>
          </div>

          {error && (
            <div className="error p-4 mb-6 bg-red-50 border-l-4 border-red-500 text-red-700 rounded-md flex items-start">
              <div>
                <p className="font-semibold">Error</p>
                <p>{error}</p>
              </div>
            </div>
          )}

          {loading && !trainStatus && (
            <div className="loadingSpinner flex flex-col items-center justify-center py-10 text-blue-600">
              <svg className="animate-spin h-10 w-10 text-blue-600" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              <p className="mt-3 text-lg font-medium">Fetching train data...</p>
            </div>
          )}

          {trainStatus && (
            <div className="statusCard bg-blue-50 border border-blue-200 rounded-xl p-6 mt-6 animate-fadeIn">
              <div className="trainHeader flex flex-col sm:flex-row items-start sm:items-center justify-between mb-6 pb-4 border-b border-blue-200">
                <div>
                  <h3 className="trainName text-xl md:text-2xl font-semibold text-blue-800">
                    {trainStatus.trainName}
                  </h3>
                  <p className="text-sm text-blue-600 mt-1">
                    {trainStatus.sourceStationName} to {trainStatus.destinationStationName}
                  </p>
                </div>
                <span className="trainNumber mt-2 sm:mt-0 px-3 py-1.5 bg-blue-100 text-blue-800 rounded-full text-sm font-medium whitespace-nowrap">
                  #{trainStatus.trainNumber}
                </span>
              </div>
              
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-x-6 gap-y-5">
                <StatusItem 
                  label="Current Status"
                  value={trainStatus.isRunning ? "Running" : "Not Running / Arrived"}
                  valueClassName={trainStatus.isRunning ? "text-green-600" : "text-slate-600"}
                />
                <StatusItem 
                  label="Last Reported At"
                  value={`${trainStatus.currentStationName} (${trainStatus.currentStationCode || 'N/A'})`}
                />
                 <StatusItem 
                  label="Delay"
                  value={`${trainStatus.delayMinutes || 0} minutes`}
                  valueClassName={trainStatus.delayMinutes > 0 ? 'text-red-600 font-bold' : 'text-green-600'}
                />
                <StatusItem 
                  label="Expected Arrival (at current)"
                  value={trainStatus.eta || 'N/A'}
                />
                <StatusItem 
                  label="Expected Departure (from current)"
                  value={trainStatus.etd || 'N/A'}
                />
                <StatusItem 
                  label="Platform"
                  value={trainStatus.platform || 'N/A'}
                />
                 {trainStatus.upcomingStations && trainStatus.upcomingStations.length > 0 && (
                  <StatusItem
                    label="Next Stop"
                    value={`${trainStatus.upcomingStations[0].station_name} (${trainStatus.upcomingStations[0].station_code})`}
                  />
                )}
                {trainStatus.upcomingStations && trainStatus.upcomingStations.length > 0 && trainStatus.upcomingStations[0].distance_from_current_station_txt && (
                    <StatusItem
                      label="Distance to Next Stop"
                      value={trainStatus.upcomingStations[0].distance_from_current_station_txt}
                    />
                )}
              </div>

              <div className="lastUpdated mt-6 pt-4 border-t border-blue-200 text-sm text-center text-blue-700">
                Last updated: {new Date(trainStatus.statusAsOf).toLocaleString()}
              </div>
            </div>
          )}

            {trainList.length > 0 && !trainStatus && !loading && !error && (
              <div className="mt-12">
                <h3 className="text-xl font-semibold text-slate-700 mb-4">
                  Or check a popular train:
                </h3>
                <div className="bg-white rounded-xl shadow-md border border-slate-100 divide-y divide-slate-100 overflow-hidden">
                  {trainList.map((train) => (
                    <div
                      key={train.train_number}
                      className="group p-4 hover:bg-slate-50 cursor-pointer transition-colors duration-150"
                      onClick={() => handleTrainSelect(train.train_number)}
                      role="button"
                      tabIndex={0}
                      onKeyPress={(e) => e.key === 'Enter' && handleTrainSelect(train.train_number)}
                    >
                      <div className="flex justify-between items-center">
                        <div>
                          <h4 className="font-medium text-slate-800 group-hover:text-blue-600 transition-colors">
                            {train.train_name}
                          </h4>
                          <p className="text-sm text-slate-500">
                            {train.source} â†’ {train.destination}
                          </p>
                        </div>
                        <span className="text-sm text-blue-600 font-semibold">
                          #{train.train_number}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
        </main>
      </div>
      <style jsx global>{`
        /* Global styles from your example - these define the base look */
        .container {}
        .card {}
        .header {}
        .title {}
        .subtitle {}
        .content {}
        .searchBox {}
        .input {}
        .input:focus {}
        .searchButton {}
        .searchButton:hover {}
        .searchButton:disabled {}
        .error {}
        .statusCard {}
        .trainHeader {}
        .trainName {}
        .trainNumber {}
        .grid {} /* This class is used by Tailwind, not directly by a specific element for global styling here */
        
        /* This specific style for StatusItem children is targeted by the StatusItem component's Tailwind classes now */
        /*
        .statusItem { 
          background-color: #ffffff; 
          padding: 0.75rem 1rem; 
          border-radius: 0.75rem; 
          box-shadow: 0 2px 8px rgba(0,0,0,0.03); 
        }
        */
        .label {}
        .value {}
        .delay {}
        .lastUpdated {}

        @keyframes fadeIn {
          from { opacity: 0; transform: translateY(10px); }
          to { opacity: 1; transform: translateY(0); }
        }
        .animate-fadeIn {
          animation: fadeIn 0.5s ease-out forwards;
        }
      `}</style>
    </div>
  );
}

// StatusItem Component with refined Tailwind classes
const StatusItem = ({ label, value, valueClassName, icon }) => (
  <div className="bg-white px-4 py-3 rounded-xl shadow-[0_1px_3px_rgba(0,0,0,0.04),0_1px_2px_rgba(0,0,0,0.02)] border border-slate-100 flex flex-col">
    {/* Adjusted shadow to be very subtle, closer to the original global .statusItem shadow, but using a slightly more standard Tailwind approach.
        Original global was: box-shadow: 0 2px 8px rgba(0,0,0,0.03);
        Current Tailwind attempts to approximate this: shadow-sm is "0 1px 2px 0 rgb(0 0 0 / 0.05)"
        Using an arbitrary shadow: shadow-[0_2px_8px_rgba(0,0,0,0.03)] would be exact.
        Let's use a slightly softer, standard shadow like 'shadow' or a combination.
        shadow-[0_1px_3px_rgba(0,0,0,0.04),0_1px_2px_rgba(0,0,0,0.02)] is a bit softer than shadow-sm.
    */}
    <span className="label text-xs sm:text-sm font-medium text-slate-500 mb-1 flex items-center">
      {icon && <span className="mr-2 text-slate-400">{icon}</span>}
      {label}
    </span>
    <span className={`value text-base font-semibold text-slate-700 ${valueClassName || ''}`}>
      {value !== undefined && value !== null && String(value).trim() !== '' ? value : 'N/A'}
    </span>
  </div>
);